<template>
    <section>
        <div class="row">
            <div class="col-xl-3 col-sm-12 col-md-6">
                <a href="javascript:void(0)" @click="expedienteTotal">
                    <div class="card bg-primary tarjet">
                        <div class="card-body py-3">
                            <div class="row">
                                <div class="d-flex">
                                    <i data-feather="briefcase" class="text-white"></i>
                                    <p class="text-white fw-bold ml-1" style="font-size: 12px; margin-top: 3px;">Total de
                                        Expedientes</p>
                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex">
                                    <h3 class="m-b-0 text-white">{{ numberOfExpedientes }}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
            <div class="col-xl-3 col-sm-6">
                <a href="javascript:void(0)" @click="expedienteTramite">
                    <div class="card bg-primary tarjet">
                        <div class="card-body py-3">
                            <div class="row">
                                <div class="d-flex">
                                    <i data-feather="file-text" class="text-white"></i>
                                    <p class="text-white fw-bold ml-1" style="font-size: 12px; margin-top: 3px;">Expedientes
                                        en Tramite</p>
                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex">
                                    <h3 class="m-b-0 text-white">{{ expedientesEnProceso }}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
            <div class="col-xl-3 col-sm-6">
                <a href="javascript:void(0)" @click="expedienteEjecucion">
                    <div class="card bg-primary tarjet">
                        <div class="card-body py-3">
                            <div class="row">
                                <div class="d-flex">
                                    <i data-feather="file" class="text-white"></i>
                                    <p class="text-white fw-bold ml-1" style="font-size: 12px; margin-top: 3px;">Expedientes
                                        en Ejecucion</p>
                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex">
                                    <h3 class="m-b-0 text-white">{{ expedientesEnEjecucion }}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
            <div class="col-xl-3 col-sm-6">
                <router-link to="/sisge/demandantes">
                    <div class="card bg-primary tarjet">
                        <div class="card-body py-3">
                            <div class="row">
                                <div class="d-flex">
                                    <i data-feather="users" class="text-white"></i>
                                    <p class="text-white fw-bold ml-1" style="font-size: 12px; margin-top: 3px;">Demandantes
                                    </p>
                                </div>
                            </div>
                            <div class="row">
                                <div class="d-flex">
                                    <h3 class="m-b-0 text-white">{{ numberOfDemandantes }}</h3>
                                    <i data-feather="chevron-right" class="text-white ml-auto" style="margin-top:6px"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </router-link>
            </div>
        </div>
    </section>
</template>
<script>
import feather from 'feather-icons';
import axios from 'axios';
export default {
    name: 'CardHomeAdmin',
    data() {
        return {
            numberOfExpedientes: 0,
            expedientesEnProceso: 0,
            expedientesEnEjecucion: 0,
            numberOfDemandantes: 0,
        };
    },
    mounted() {
        this.fetchDataAdmin();
        this.$nextTick(() => {
            feather.replace();
        });
    },

    methods: {
        async fetchDataAdmin() {
            try {
                const url = `${this.$store.getters.get__url}/reportes/inicio`;
                const token = this.$store.getters.get__token;

                const response = await axios.post(
                    url,
                    {},
                    {
                        headers: {
                            Authorization: `Bearer ${token}`,
                        },
                    }
                );

                const responseData = response.data;
                this.numberOfExpedientes = responseData.exptotal;
                this.expedientesEnProceso = responseData.exptramite;
                this.expedientesEnEjecucion = responseData.expejecucion;
                this.numberOfDemandantes = responseData.demandante;
            } catch (error) {
                console.error(error);
            }
        },
        expedienteTotal() {
            this.$router.push({ name: 'expediente/lista', params: { tipo_exp: '' } });
        },
        expedienteTramite() {
            this.$router.push({ name: 'expediente/lista', params: { tipo_exp: 'En Tramite' } });
        },
        expedienteEjecucion() {
            this.$router.push({ name: 'expediente/lista', params: { tipo_exp: 'En Ejecucion' } });
        },
    }
}
</script>
<style scoped>
.tarjet {
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.tarjet:hover {
    /* transform: scale(1.010); */
    background-color: #6610f2 !important;
    transition: 0.25s;
    /* box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px; */
}

@media (min-width: 768px) {
    .tarjet {
        height: 100px !important;
    }

    .tarjeta-data {
        margin-top: -7.5px;
    }

    .tarjeta-data-4 {
        margin-top: 5px;
    }

    .row-link {
        border-right: 1px solid #e1e1e1;
    }
}

@media (max-width: 768px) {
    .row-link {
        border-bottom: 1px solid #e1e1e1;
    }
}
</style>